$tile-gap: 24px;

.promotion-area-wrap {
	position:relative;
	max-width:(1284px+30px);
	margin:26px auto 50px auto;
	@include screen(custom, min, $min-md) { //768
		font-size: 0;
		padding:0 15px;
		@include screen(custom, max, $max-lg) {
			padding:0 15px;
		}
	}
	@include screen(custom, max, $max-sm) {
		margin:20px -15px 0;
	}

	.tile {
		position:relative;
		margin: $tile-gap 0 0 0;
		@include screen(custom, max, $max-sm) {
			$tile-gap: 20px;
			margin: $tile-gap 0 0 0;
			&:first-child {
				margin-top: 0;
			}
		}
		.visual {
			@include screen(custom, max, $max-sm) {
				position:relative;
				width:100%;
				height:370px;
				overflow:hidden;
			}
			img {
				max-width:100%;
				&.pc {display:inline-block;}
				&.mobile {display:none;}
				@include screen(custom, max, $max-sm) {
					max-width:none;
					width:auto;
					height:370px;
					position:absolute;
					left:50%;
					transform:translateX(-50%);
					&.pc {display:none;}
					&.mobile {display:inline-block;}
				}
			}
		}

		// desktop-tablet layout
		@include screen(custom, min, $min-md) { //768
			width: 50%;
			display: inline-block;
			&:nth-child(odd) {
				.visual {
					margin-right: $tile-gap/2;
				}
				.text-block {
					padding-right: $tile-gap/2;
				}
			}
			&:nth-child(even) {
				.visual {
					margin-left: $tile-gap/2;
					text-align: right;
				}
				.text-block {
					padding-left: $tile-gap/2;
				}
			}
		}
	}
	.text-block {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		text-align: center;
		margin-top: 48px;
		@include screen(custom, max, $max-lg) {
			margin-top:40px;
		}
		@include screen(custom, max, $max-md) {
			margin-top: 25px;
		}

		.name {
			width:420px;
			margin:0 auto;
			@include font-family($font-semibold);
			font-size:40px;
			line-height:48px;
			@include screen(custom, max, $max-lg) { //1199
				font-size:34px;
				line-height:40px;
			}
			@include screen(custom, max, $max-md) { //991
				width:100%;
				padding:0 30px;
				font-size:28px;
				line-height:36px;
			}
			@include screen(custom, max, $max-sm) {
				max-width:(320px+48px);
				padding:0 24px;
			}
		}
		.info {
			width:340px;
			margin:0 auto;
			@include font-family($font-regular);
			font-size:16px;
			line-height:24px;
			margin-top: 4px;
			@include screen(custom, max, $max-md) {
				width:100%;
				padding:0 30px;
				font-size:14px;
				line-height:20px;
				margin-top: 6px;
			}
			@include screen(custom, max, $max-sm) {
				max-width:(320px+48px);
				padding:0 24px;
			}
		}

		&.dark {
			.name {
				color:$color-black;
			}
			.info {
				color:$color-nightrider;
			}
		}
		&.white {
			.name, .info {
				color:$color-white;
			}
		}
	}
	// 20200608 START 구유정 || RTL 추가
	[dir=rtl] & { // .promotion-area-wrap
		.tile {
			// desktop-tablet layout
			@include screen(custom, min, $min-md) { //768
				&:nth-child(odd) {
					.visual {
						margin-left: $tile-gap/2;
						margin-right: 0;
					}
					.text-block {
						padding-left: $tile-gap/2;
						padding-right: 0;
					}
				}
				&:nth-child(even) {
					.visual {
						margin-left: 0;
						margin-right: $tile-gap/2;
						text-align: left;
					}
					.text-block {
						padding-left: 0;
						padding-right: $tile-gap/2;
					}
				}
			}
		}
	}
	// 20200608 END 구유정 || RTL 추가
}