@charset 'UTF-8';
@import './setting/variables';

/* LGEPE-73 20210106 edit */
.support-carousel-wrap{
  position: relative;
  clear: both;
  max-width: 1600px;
  margin: 0 auto;
	@include screen(custom, max, 1600) {
		margin:0 -15px;
	}
}
/* //LGEPE-73 20210106 edit */
.support-carousel {
  position: relative;
  margin: 0 auto;
	$relative: 1200px;
	$breakpoint: 1600;

	// image box area
	.carousel-wrap {
		position:relative;
		width:100%;
		clear:both;
		overflow:hidden;
		opacity:0;
		@extend %clearfix;
		&.active {
			opacity:1;
		}
		.slick-list {
			width:auto;
			height:100%;
			overflow:hidden;
		}
		.slick-track {
			&:after {
				content:'';
				display:block;
				width:100%;
				height:0;
				clear:both;
			}
		}
		/* LGEPE-73 20210106 edit */
		.slick-arrow {
			overflow: hidden;
			padding:0;
			cursor:pointer;
			&.slick-prev,
			&.slick-next {
				position:absolute;
				z-index:99;
				width:22px;
				top:50%;
				height:40px;
				transform: translate(0,-50%);
				pointer-events: auto;
				[dir="rtl"] & {
					transform: translate(0,-50%) rotate(180deg);
				}
			}
			&.slick-prev {
				left:40px;
				[dir="rtl"] & {
					left:auto;
					right:40px;
				}
			}
			&.slick-next {
				right:40px;
				[dir="rtl"] & {
					right:auto;
					left:40px;
				}
			}
			@include screen(custom, max, $max-lg) {
				&.slick-prev {
					left:30px;
					[dir="rtl"] & {right:30px;}
				}
				&.slick-next {
					right:30px;
					[dir="rtl"] & {left:30px;}
				}
			}
			&.slick-disabled {
				opacity:0.3;
			}
		}
		.carousel-box {
			position:relative;
			width:100%;
			float:left;
			overflow: hidden;
			[dir="rtl"] & {float:right;}
		}
		/* //LGEPE-73 20210106 edit */
		.visual-area {
			position:relative;
			overflow:hidden;
			z-index:1;
			img {
				position:relative;
				width:100%;
				height:auto;
				/*
				width:1600px;
				left:50%;
				transform:translateX(-50%);
				@include screen(custom, max, $max-lg) {
					width:(1600px*100%/1200px);
				}
				*/
				&.pc {display:inline;}
				&.mobile {display:none;}
				@include screen(custom, max, $max-sm) {
					&.pc {display:none;}
					&.mobile {display:inline;}
				}
				&.lazyload,
				&.lazyloading {
					opacity:0;
					&.lazyloaded {
						opacity:1;
					}
				}
			}
		}
		.carousel-box {
			position:relative;
			width:100%;
			overflow:hidden;
			// font black
			&.text-black {
				background-color: $bg-white;
				.text-wrap {
					.title, .title h1, .title h2, .title h3, .title h4, .title h5, .title h6 {
						color:$color-black;
					}
					.copy {
						color:#333;
					}
					.eyebrow, .nugget {
						color:$color-nightrider;
					}
					.nugget-eyebrow .eyebrow-box:after {background-color:$bg-dimgray;}
					.date {
						color:$color-black;
					}
					.cta {
						.link-text {
							color:$color-nightrider;
							&:after {
								background-image:url('/lg5-common-gp/images/common/icons/link-right-nightrider.svg');
							}
						} 
						//.btn-outline-secondary {
						//	border-color:$line-dimgray !important;;
						//	color:$color-dimgray !important;;
						//}
					}
					.video-link {
						.link-text {color:$color-nightrider;}
						.ico-left {
							&:before {
								background-image:url('/lg5-common-gp/images/components/btn-play-black.svg');
							}
						}
					}
				}
			}
			// font white
			&.text-white {
				background-color: $bg-gray;
				.text-wrap {
					color:$color-white;
					.eyebrow,
					.title {
						color:$color-white;
					}
					.cta {
						.link-text {
							color:$color-white !important;
							&:after {
								background-image:url('/lg5-common-gp/images/common/icons/link-right-white.svg');
							}
						} 
						.btn-outline-secondary {
							border-color:$line-white !important;
							color:$color-white !important;
							&:hover, &:focus {
								border-color:$line-white !important;
								background-color:$color-white !important;
								color:$color-black !important;
							}
						}
					}
					.video-link {
						.link-text {color:$color-white;}
						.ico-left {
							&:before {
								background:url('/lg5-common-gp/images/components/btn-play.svg') no-repeat;
								background-size:20px 20px;
							}
						}
					}
				}
			}
		}
		.text-area {
			position:absolute;
			left:0;
			padding:0 30px;
			@include screen(custom, max, $max-sm) {
				padding:0 24px;
			}
			width:100%;
			z-index:2;
			&.align-left {
				.text-wrap .inner {
					float:left;
					text-align:left;
				}
			}
			&.align-right {
				.text-wrap .inner {
					float:right;
					text-align:left;
				}
			}
			&.align-left,
			&.align-right {
				.text-wrap {
					.cta,
					.video-link {
						clear:both;
						float:left;
						text-align:left;
					}
					.cta-button {
						.btn {margin:0 6px 10px 0;}
					}
					.cta-link {
						.link-text {margin:0 18px 11px 0;}
					}
					.nugget-eyebrow {
						clear:both;
						float:left;
						text-align:left;
						.eyebrow-box {
							max-width:100%;
							clear:both;
							display:block;
							padding-left:0;
							padding-bottom:10px;
							&:after {
								display:none;
							}
							&:last-child {
								padding-bottom:0;
							}
						}
						@include screen(custom, max, $max-sm) {
							.eyebrow-box {
								padding-left:0;
								padding-right:0;
							}
						}
					}
					
				}
			}
			&.align-center {
				.text-wrap .inner {
					float:none;
					margin:0 auto;
				}
				.text-wrap {
					
					.text-block {
						margin:0 auto;
						text-align:center;
					}
					.cta-button {
						.btn {margin:0 3px 10px 3px;}
					}
					.cta-link {
						.link-text {margin:0 9px 11px 9px;}
					}
					
				}
			}
			&.align-middle {
				top:50%;
				transform:translateY(-50%);
				@include screen(custom, min, $min-md) {
					.text-wrap {
						.inner {
							.cta {
								&:last-child {
									margin-bottom:-23px;
								}
							}
							.video-link {
								&:last-child {
									margin-bottom:-23px;
								}
							}
						}
					}
				}
			}
		}
		.text-wrap {
			position:relative;
			width:100%;
			max-width:1284px;
			margin:0 auto;
			.eyebrow {
				@include font-family($font-semibold);
			}
			.title, .title h1, .title h2, .title h3, .title h4, .title h5, .title h6 {
				@include font-family($font-semibold);
			}
			.date {
				@include font-family($font-bold);
			}
			.nugget-eyebrow {
				font-size:0;
				display:table;
				margin-left:auto;
				margin-right:auto;
				
				.eyebrow-box {
					position:relative;
					display:table-cell;
					max-width:324px;
					padding:0 25px;
					vertical-align:middle;
					&:after {
						@include pseudo();
						position:absolute;
						right:0; 
						top:50%; 
						margin-top:-16px;
						width:1px; 
						height:32px;
						background:#c0c0c0;
					}
					&:last-child {
						&:after {
							display:none;
						}
					}
					@include screen(custom, max, $max-sm) {
						max-width:100%;
						display:block;
						padding-bottom:15px;
						margin-bottom:12px;
						padding-left:0;
						padding-right:0;
						&:after {
							right:auto;
							top:auto;
							margin-top:0;
							width:26px;
							height:1px;
							bottom:0;
							left:50%;
							margin-left:-12px;
						}
					}
				}
			}
			.cta {
				.cta-button {
					// text link에 위아래 8픽셀의 여백이 있어서 맞춰주기 위해 버튼에도 동일하게 처리함
					padding-top:8px;
					padding-bottom:8px;
				}
			}
		}
		.text-area {
			&.align-left .text-wrap,
			&.align-right .text-wrap {
				padding-left:70px;
				padding-right:70px;
				max-width:(1284px+140px);
				@include screen(custom, max, $max-lg) {
					padding-left:50px;
					padding-right:50px;
				}
				@include screen(custom, max, ($max-md)) {
					padding-left:30px;
					padding-right:30px;
				}
			}
		}
		.type-wide {
			.text-wrap .inner {
				max-width:700px;
				@include screen(custom, max, 1600) {
					max-width:(700px*100%/(1600px-290px));
				}
				@include screen(custom, max, $max-lg) {
					max-width:(460px*100%/(992px-160px));
				}
				@include screen(custom, max, ($max-md)) {
					max-width:(350px*100%/(768px-120px));
				}
				@include screen(custom, max, $max-sm) {
					max-width:100%;
				}
			}
			&.align-center {
				.text-wrap .inner {
					max-width:100%;
					.text-block {
						margin-left:auto;
						margin-right:auto;
						max-width:900px;
						@include screen(custom, max, $breakpoint) {
							max-width:(900px*100%/$relative);
						}
						@include screen(custom, max, $max-sm) {
							max-width:100%;
						}
					}
				}
			}
		}
		.type-narrow {
			.text-wrap .inner {
				width:100%;
				max-width:500px;
				@include screen(custom, max, 1293) {
					max-width:(465px*100%/(1200px-200px));
				}
				@include screen(custom, max, $max-lg) {
					max-width:(390px*100%/(992px-160px));
				}
				@include screen(custom, max, ($max-md)) {
					max-width:(300px*100%/(768px-120px));
				}
				@include screen(custom, max, $max-sm) {
					max-width:100%;
				}
			}
			&.align-center {
				.text-wrap .inner {
					max-width:100%;
					.text-block {
						margin-left:auto;
						margin-right:auto;
						max-width:700px;
						@include screen(custom, max, $breakpoint) {
							max-width:(700px*100%/$relative);
						}
						@include screen(custom, max, $max-sm) {
							max-width:100%;
						}
					}
					
				}
			}
		}
		&.type-hero {
			.text-area {
				&.align-top {
					top:82px;
					@include screen(custom, max, $max-lg) {
						top:52px;
					}
					@include screen(custom, max, $max-sm) {
						top:32px;
					}
				}
				/*
				&.align-bottom {
					bottom:82px;
					@include screen(custom, max, $max-lg) {
						bottom:52px;
					}
					@include screen(custom, max, $max-sm) {
						bottom:32px;
					}
				}
				*/
				@include screen(custom, max, $max-sm) {
					&.align-mobile-top {
						top:32px;
						bottom:auto;
						transform: none;
					}
					&.align-mobile-middle {
						top:50%;
						bottom:auto;
						transform:translateY(-50%);
					}
					/*
					&.align-mobile-bottom {
						top:auto;
						bottom:32px;
						transform: none;
					}
					*/
				}
			}
			.text-wrap {
				@extend %clearfix;
				.eyebrow {
					font-size:30px;
					line-height:36px;
					margin-bottom:3px;
					@include screen(custom, max, $max-lg) {
						font-size:18px;
						line-height:24px;
						margin-bottom:4px;
					}
					@include screen(custom, max, $max-sm) {
						font-size:16px;
						line-height:24px;
						margin-bottom:2px;
					}
				}
				.title {
					margin-bottom:15px;
					@include screen(custom, max, $max-lg) {
						margin-bottom:12px;
					}
					@include screen(custom, max, $max-sm) {
						margin-bottom:10px;
					}
				}
				.title, .title h1, .title h2, .title h3, .title h4, .title h5, .title h6 {
					font-size:60px;
					line-height:60px;
					@include screen(custom, max, 1355) {
						font-size:48px;
						line-height:50px;
					}
					@include screen(custom, max, $max-lg) {
						font-size:36px;
						line-height:40px;
					}
					@include screen(custom, max, $max-sm) {
						font-size:32px;
						line-height:36px;
					}
				}
				.copy {
					font-size:20px;
					line-height:26px;
					margin-bottom:18px;
					@include screen(custom, max, $max-lg) {
						font-size:16px;
						line-height:24px;
						margin-bottom:15px;
					}
					@include screen(custom, max, $max-sm) {
						//display:none;
						font-size:16px;
						line-height:24px;
						margin-bottom:13px;
					}
				}
				.cta {
					padding-bottom:5px;
				}
				.video-link {
					padding-bottom:24px;
					@include screen(custom, max, $max-sm) {
						padding-bottom:14px;
					}
				}
			}
			// slick
			&.slick-initialized {
				.text-area {
					/*
					&.align-bottom {
						// Hero 캐로젤 일 경우, 모바일에서 ...과 텍스트 영역 위치 겹침 해결
						@include screen(custom, max, $max-sm) {
							bottom:42px;
						}
					}
					*/
					@include screen(custom, max, $max-sm) {
						&.align-left .text-wrap,
						&.align-right .text-wrap {
							padding-left:0;
							padding-right:0;
						}
						// 모바일 사이즈에서는 중앙정렬이므로 불필요
						&.align-left .text-wrap .inner {
							padding-left:0;
							float: none;
						}
						&.align-right .text-wrap .inner {
							padding-right:0;
						}
					}
					.text-block {
						user-select: none;
					}
				}
				.animation-box .animation-area .controller-wrap {
					margin-bottom:(42px + 15px);
					@include screen(custom, max, $max-sm) {
						margin-bottom:(42px + 12px);
					}
				}
			}
			// hero에서는 모바일일 경우 텍스트가 무조건 센터 정렬
			@include screen(custom, max, $max-sm) {
				.align-left,
				.align-right {
					.text-wrap {
						.text-block,
						.cta, 
						.video-link,
						.nugget-eyebrow {
							float:none;
							text-align:center;
						}
					}
				}
			}
		}
		&.type-etc {
			.text-area {
				&.align-top {
					top:77px;
					@include screen(custom, max, $max-lg) {
						top:52px;
					}
					@include screen(custom, max, $max-sm) {
						top:32px;
					}
				}
				&.align-center {
					@include screen(custom, max, $max-sm) {
						// Hero 가 아닌 경우, pos-center 일 때, 모바일에서 tech spec 출력되지 않음
						.nugget-eyebrow {
							display:none;
						}
					}
				}
				&.align-bottom {
					.text-wrap {
						.video-link {
							@include screen(custom, min, ($min-md)) {
								&:last-child {
									padding-bottom:24px;
								}
							}
						}
					}
				}
				&.align-left,
				&.align-right {
					@include screen(custom, max, $max-sm) {
						// Hero 가 아닌 경우, pos-left, pos-right 일 때, 모바일에서 텍스트 영역 분리됨.
						position:static;
						transform:none;
						padding-top:30px;
						padding-bottom:30px;
						&.text-black {background:$bg-white;}
						&.text-white {background:$bg-nightrider;}
						.text-wrap {
							padding-left:0;
							padding-right:0;
							.inner {
								float:none;
								.text-block,
								.cta,
								.video-link,
								.nugget-eyebrow {
									float:none;
								}
								.nugget-eyebrow {
									width:100%;
								}
							}
							.video-link {
								&:last-child {
									padding-bottom:0;
								}
							}
						}
					}
				}
				&.align-middle {
					.text-wrap {
						.video-link {
							&:last-child {
								padding-bottom:0;
							}
						}
					}
				}
			}
			.text-wrap {
				.eyebrow {
					font-size:30px;
					line-height:36px;
					margin-bottom:8px;
					@include screen(custom, max, $max-lg) {
						font-size:18px;
						line-height:24px;
						margin-bottom:2px;
					}
					@include screen(custom, max, $max-sm) {
						font-size:16px;
						line-height:24px;
						margin-bottom:2px;
					}
				}
				.title {
					margin-bottom:19px;
					@include screen(custom, max, $max-lg) {
						margin-bottom:17px;
					}
					@include screen(custom, max, $max-sm) {
						margin-bottom:10px;
					}
				}
				.title, .title h1, .title h2, .title h3, .title h4, .title h5, .title h6 {
					font-size:60px;
					line-height:60px;
					@include screen(custom, max, 1355) {
						font-size:48px;
						line-height:50px;
					}
					@include screen(custom, max, $max-lg) {
						font-size:36px;
						line-height:40px;
					}
					@include screen(custom, max, $max-sm) {
						font-size:32px;
						line-height:36px;
					}
				}
				.date {
					font-size:20px;
					line-height:26px;
					margin-bottom:4px;
					@include screen(custom, max, $max-lg) {
						font-size:18px;
						line-height:24px;
						margin-bottom:0px;
					}
					@include screen(custom, max, $max-sm) {
						font-size:16px;
						line-height:24px;
						margin-bottom:2px;
					}
				}
				.copy {
					font-size:20px;
					line-height:26px;
					margin-bottom:18px;
					@include screen(custom, max, $max-lg) {
						font-size:16px;
						line-height:24px;
						margin-bottom:17px;
					}
					@include screen(custom, max, $max-sm) {
						font-size:16px;
						line-height:24px;
						margin-bottom:12px;
					}
				}
				.cta {
					padding-bottom:3px;
				}
				.video-link {
					padding-bottom:36px;
					@include screen(custom, max, $max-lg) {
						padding-bottom:25px;
					}
					@include screen(custom, max, $max-sm) {
						padding-bottom:15px;
					}
				}
				.nugget-eyebrow {
					.eyebrow {
						font-size:18px;
						line-height:24px;
						// margin-bottom:0;
						margin-bottom: 6px;
						@include font-family($font-regular);
						@include screen(custom, max, $max-lg) {
							font-size:14px;
							line-height:20px;
						}
						@include screen(custom, max, $max-sm) {
							padding-top:4px;
						}
					}
					.nugget {
						font-size:20px;
						line-height:26px;
						@include font-family($font-semibold);
						@include screen(custom, max, $max-lg) {
							font-size:16px;
							line-height:24px;
						}
						@include screen(custom, max, $max-sm) {
							padding-top:7px;
						}
					}
				}
			}
		}

	}
	/* LGEPE-73 20210106 edit */
	// slick slider 
	.slick-slider {
		opacity:0;
		&.slick-initialized {
			opacity:1;
		}
		button {
			appearance:none;
			border:none;
			background-color:transparent;
			text-indent:-9999em;
			[dir="rtl"] & {text-indent:9999em;}
		}
		.slick-arrow {
			line-height:0;
			color:$color-nightrider;
			svg {
				display:block;
			}
			&:not([disabled]):hover,
			&:not([disabled]):focus {
				color:$color-black;
			}
			@include screen(custom, max, $max-sm) {
				display:none !important;// 모바일에서는 좌우 화살표 출력하지 않음
			}
		}
	}
	.slick-white.slick-slider {
		.slick-arrow {
			color:#ccc;
			&:not([disabled]):hover,
			&:not([disabled]):focus {
				color:#fff;
			}
		}
	}
	.slick-indicator {
		position:absolute;
		z-index:2;
		bottom:0;
		left:0;
		text-align:center;
		width:100%;
		height:42px;
		@include screen(custom, max, $max-sm) {
			height:32px;
		}
		.slick-dot-wrap {
			display: inline-block;
			vertical-align:top;
		}
		.slide-pause {
			display:none;
			overflow:hidden;
			white-space:nowrap;
			vertical-align:top;
			width:12px;
			height:14px;
			background:url('/lg5-common-gp/images/common/icons/pause-white-lg.svg') no-repeat;
			background-size:100% 100%;
			color:transparent;
			margin-top:-1px;
			margin-left:17px - 5px;
			@include screen(custom, max, $max-sm) {
				margin-top:-2px;
				margin-left:17px - 4px;
			}
			[dir="rtl"] & {
				margin-left:0;
				margin-right:17px - 5px;
				@include screen(custom, max, $max-sm) {
					margin-right:17px - 4px;
				}
			}
			&.play {
				background-image:url('/lg5-common-gp/images/common/icons/play-white-lg.svg');
			}
			&.active {
				display:inline-block;
			}
		}
		.slick-dots {
			display:inline-block;
			font-size: 0;
			text-align: center;
			margin:0;
			height:12px;
			vertical-align: top;
			@include screen(custom, max, $max-sm) {
				height:10px;
			}
			li {
				position:relative;
				display:inline-block;
				vertical-align:top;
				width:12px;
				height:12px;
				margin:0 5px;
				border-radius:50%;
				border:1px solid rgba(0,0,0, 0.5);
				line-height:normal;
				@include screen(custom, max, $max-sm) {
					width:10px;
					height:10px;
					margin:0 4px;
				}
				a, button {
					background: $color-white;
					display:block;
					width:100%;
					height:100%;
					margin:0;
					padding:0;
					border:0;
					border-radius:50%;
					overflow:hidden;
					color:transparent;
					cursor:pointer;
					&:focus {
						outline:1px dotted;
						outline:5px auto -webkit-focus-ring-color;
					}
				}
				&.slick-active {
					border:1px solid $color-carmine;
					a, button {
						background:$color-carmine;
						&:focus {		
							outline: 2px solid #000 !important;/* LGECI-297 : 20201214 edit */
						}
					}
				}
				&:only-child {
					display:none;
				}
			}
		}
	}
	/* //LGEPE-73 20210106 edit */
	&.js-invisible-nav {
		.slick-indicator,
		.slick-arrow {
			z-index: -1 !important;
		}
	}

	// bottom
	&.component {
		.carousel-wrap.type-hero,
		.carousel-wrap.type-etc {
			// align-bottom margin (for desktop)
			@include screen(custom, min, $min-md) {
