.notices-bar-wrap {
	position: relative;
	padding: 0 30px;
	max-width:$max-design+px;
	margin:0 auto;
	@include screen(custom, max, $max-design+30) {
		margin:0 -15px;
	}
	@include screen(custom, max, $max-sm) { // ~ 767
		padding: 0 25px;
		&:after {
			content: '';
			display: block;
			width: auto;
			height: 20px;
			background: #f2f2f2;
			margin: 0 -25px 20px -25px;
		}
	}
	@include screen(custom, min, $min-md) { // 768 ~
		border-bottom: 1px solid #e4e4e4;
	}
	// notices bar layout
	.notices-bar {
		display:block;
		max-width:1284px;
		box-sizing: border-box;
		margin: 0 auto;
		position: relative;
		.notices-list {
			&,
			.list-item {
				height: 26px;
				line-height: 26px;
				outline: none;
			}
		}
		.title {
			display: block;
			@include font-family($font-bold);
			color: $color-carmine;
			font-size: 16px;
			text-transform: uppercase;
			left: 0;
			line-height: 26px;
			@include screen(custom, max, $max-sm) {
				margin: 13px 0 5px 0;
			}
		}
		// column style layout (only for desktop & tablet)
		@include screen(custom, min, $min-md) { // 768 ~
			display:table;
			width:100%;
			height: 59px;
			.title {
				display:inline-block;
				width:auto;
				margin: 17px 0;
				margin-right:30px;
				white-space: nowrap;
			}
		}
	}
	.notices-list-wrap {
		.carousel-btn-wrap {
			text-align: center;
		}
		// notices bar carousel column layout (only for desktop & tablet)
		@include screen(custom, min, $min-md){
			display:table-cell;
			position: relative;;
			width:100%;
			$ctrlBoxWidth: 102px; // carousel controller width
			padding:17px 0;
			vertical-align:middle;
			.notices-list {
				width:calc(100% - 117px);
				margin-right:-117px;
			}
			.carousel-btn-wrap {
				position: absolute;
				right: 0;
				top: 17px;
				width: $ctrlBoxWidth;
				button {cursor: pointer;}
			}
		}
	}
	

	// notices-list (text) style
	.notices-list {
		position: relative;
		overflow: hidden;
		
		.list-item {
			@include font-family($font-regular);
			font-size: 16px;
			line-height: 1;
			position: relative;
			@include screen(custom, min, $min-md){
				width:100% !important;
			}
			.subject {
				position: relative;
				display: inline-block;
				padding-right: 83px;
				max-width: 100%;
				margin: 0;
			}
			a {
				color: $color-nightrider;
				display: inline-block;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				max-width: 100%;
				text-decoration: underline;
				// line-height: 20px;
			}
			.date {
				// float: right;
				position: absolute;
				right: 0;
				top: 0;
				font-size: 14px;
				// line-height: 20px;
				color: $color-dimgray;
			}
		}
	}
	// square button style for carousel controller
	.carousel-btn-wrap {
		@include screen(custom, max, $max-sm) {
			margin: 12px 0 20px 0;
		}
	}
	// pause & play btn controller
	.notices-list-wrap {
		.carousel-btn-wrap {
			.slick-pause {
				display: inline-block;
			}
			.slick-play {
				display: none;
			}
		}
		&.paused {
			.carousel-btn-wrap {
				.slick-pause {
					display: none;
				}
				.slick-play {
					display: inline-block;
				}
			}
			
		}
	}
}
