// main-thumbnail-link-menu
.main-thumbnail-link-menu {
	// component common
	h1,h2,h3,h4,h5,h6 {margin: 0;}
	a {color:$color-nightrider;display:block;}
	.area-title, .menu-title {
		display:block;
		@include font-family($font-semibold);
	}
	.icon, .menu-subtext {display:inline-block;}
	.inner {max-width: (1284px+60px); padding:0 30px; margin:auto;}

	// area default style
	color:$color-nightrider; // This is to use to icon color. 
	box-sizing:border-box; 
	padding:48px 0 40px;
	word-break: keep-all;
	max-width:$max-design+px;
	margin:0 auto;
	@include screen(custom, max, $max-design+30) {
		margin:0 -15px;
	}
	// element original style
	.area-title {
		text-align: center;
		font-size:40px;
		line-height:48px;
	}
	.menu-linkbox-belt {
		margin-top: 22px;
		@extend %clearfix;
	}
	.menu-linkbox {
		float:left;
		width:(303px*100%)/1284px;
		box-sizing:border-box;
		padding:28px 0;
		text-align:center;
		&:not(:nth-child(4n)){
			margin-right:(24px*100%)/1284px;
		}
		.icon {
			max-width:64px; width:100%;
			img {
				width:100%;
			}
		}
		.menu-title {
			font-size:24px;
			line-height:32px;
			margin-top: 12px;
		}
		.menu-subtext {
			width:63%; min-width:237px;
			font-size:14px;
			line-height:20px;
			color:$color-dimgray;
			margin-top: 5px;
		}

		[dir="rtl"] & {
			float:right;
		}
		
		@include screen(custom, min, $min-xl){
			&:nth-of-type(4n+1){
				clear:both;
			}
		}
	}

	
	@include screen(custom, max, $max-lg){
		.menu-linkbox {
			width:(100%/3);
			&:nth-child(n){
				margin-right:0;
			}
			.menu-title {
				font-size:22px;
				line-height:30px;
				margin-top:18px;
			}
			.menu-subtext {
				min-width:227px;
			}
		}
	}
	@include screen(lg){
		.menu-linkbox {
			&:nth-of-type(3n+1){
				clear:both;
			}
		}
	}
	@include screen(md){
		.menu-linkbox {
			width:(100%/2);
			&:nth-of-type(2n+1){
				clear:both;
			}
		}
	}
	@include screen(custom, max, $max-sm) {
		padding:34px 0 36px;
		.inner {padding:0 24px;}
		.area-title {
			font-size:24px;
			line-height:32px;
		}
		.menu-linkbox-belt {
			margin-top:-4px;
		}
		.menu-linkbox {
			padding:37px 10px 0;
			&:nth-of-type(3n+1){
				clear:both;
			}
			.icon {
				max-width:48px;
			}
			.menu-title {
				font-size:14px;
				line-height:18px;
				margin-top:10px;
			}
			.menu-subtext {
				display:none;
			}
		}
	}
	@include screen(custom, max, 360){
		.area-title {
			font-size:22px;
			line-height:30px;
		}
	}
}