.solutions-contents {
	position:relative;
	max-width:(1284px+30px);
	margin:0 auto;
	@include screen(custom, min, $min-md) {
		padding:0 30px;
	}
	@include screen(custom, max, $max-sm) {
		margin:0 9px 30px;
	}
	.title {
		text-align:center;
		padding:38px 0 0 0;
		@include screen(custom, max, $max-sm) {
			padding: 0;
		}
		strong, h1, h2, h3, h4, h5, h6 {
			@include font-family($font-semibold);
			font-size:40px;line-height:48px;color:$color-black;
			@include screen(custom, max, $max-sm) {
				font-size:28px;line-height:36px;
			}
		}
		.desc {
			@include font-family($font-regular);
			font-size:16px;
			padding:5px 0 24px 0;
			color: $color-dimgray;
			@include screen(custom, max, $max-sm) {
				font-size:14px;line-height:18px;
				padding-top: 0;
			}
		}
	}
	.product-type {
		position:relative;
		width:100%;
		padding:0 108px;
		margin-bottom: 40px;
		// max-height:592px;
		overflow:hidden;
		@include screen(custom, max, (1284+30)) {
			padding:0 50px;
		}
		@include screen(custom, max, $max-sm) {
			padding: 0;
			margin-bottom:0;
		}

		.slick-track {
			display:flex;
			flex-wrap:wrap;
		}

		.items {
			@extend %clearfix;
			width:100%;
			.slick-list {
				width:auto;
				height:100%;
				overflow:hidden;
				@include screen(custom, max, $max-sm) {
					margin: 0 0 0 3px;
				}
			}
			.slick-arrow {
				padding:0;
				/* LGECI-297 20201126 add */
				&:not([disabled]):focus, &:not([disabled])::-moz-focus-inner {
					outline: 2px solid #000;		
					outline-offset: -2px;
				}
				/* //LGECI-297 20201126 add */
				&.slick-prev,
				&.slick-next {
					position:absolute;
					top:50%;
					width:27px;
					height:46px;
					transform: translate(0,-50%);
				}
				&.slick-prev {
					left:58px;
				}
				&.slick-next {
					right:58px;
				}
				@include screen(custom, max, (1284+30)) {
					&.slick-prev {
						left:0;
					}
					&.slick-next {
						right:0;
					}
				}
			}
			opacity:0;
			&.slick-initialized {
				opacity:1;
			}

			&.lte-2 {
				.slick-arrow {
					display: none;
				}
				.slick-dots {
					visibility: hidden;
				}
			}
			@include screen(custom, min, $min-md) {
				&.lte-3 {
					.slick-arrow {
						display: none;
					}
					.slick-dots {
						visibility: hidden;
					}
				}
			}
			@include screen(custom, min, $min-lg) {
				&.lte-4 {
					.slick-arrow {
						display: none;
					}
					.slick-dots {
						visibility: hidden;
					}
				}
			}
		}
		.item {
			position:relative;
			float:left;
			padding:20px 0 0;
			outline: none;
			@include screen(custom, max, $max-sm) {
				padding-top: 10px;
			}
			&:before {
				@include pseudo();
				width:1px;height:100%;
				background:$line-lightgray;
				left:0;top:0;
			}
			// @include screen(custom, min, $min-lg) {
			// 	&:nth-child(4n-3) {&:before {display:none;}}
			// }
			// @include screen(md) {
			// 	&:nth-child(3n-2) {&:before {display:none;}}
			// }
			// @include screen(custom, max, $max-sm) {
			// 	&:nth-child(2n-1) {&:before {display:none;}}
			// }
			.visual,
			.category {
				
				text-align: center;
				margin:0 auto;
			}
			.visual {
				// background:rgba(165,0,52,0.2);
				// img {width:100%;height:auto;}
				// width: auto;
				max-width:160px;
				img {
					width: 100%;
				}
				@include screen(custom, min, $min-md) {
					
				}
				@include screen(custom, max, $max-sm) {
					max-width:130px;
					padding: 0 15px;
					box-sizing: border-box;
					img {
						max-width: 100%;
						// width: auto !important;
					}
				}
			}
			.category {
				// min-height:290px;
				// background:rgba(165,0,52,0.2);
				width: 100%;
				margin-top:26px;
				padding: 0 5px;
				text-align:center;
				.main-category {
					//height: 59px;
					height:64px;
					line-height: 0;
					@include screen(custom, max, $max-sm) {
						height: auto;
						margin-bottom: 13px;
					}
					a {
						@include font-family($font-semibold);
						color: $color-black;
						font-size: 20px;
						line-height: 22px;
						@include screen(custom, max, $max-sm) {
							font-size: 18px;
						}
					}
				}
				.sub-category {
					li {
						margin-top: 12px;
						line-height: 18px;
						@include screen(custom, max, $max-sm) {
							margin-top: 12px;
							line-height: 16px;
						}
						&:first-child {
							margin-top: 0;
						}
						h3, h4, h5 {
							line-height: inherit;
							font-size: inherit;
						}
						a {
							@include font-family($font-regular);
							color: $color-dimgray;
							font-size: 16px;
							@include screen(custom, max, $max-sm) {
								font-size: 14px;
							}
						}
					}
				}
			}
		}

		// slick carousel stylesheet
		.slick-slider {
			button {
				appearance: none;
				border: none;
				background-color: transparent;
				text-indent: -9999em;
				line-height: 0;
				// outline: none;
			}
			.slick-track {
				// necessary for item's bleet hidden 
				margin-left: -1px;
				margin-right: -1px;
			}
			.slick-arrow {
				svg {
					display: block;
					/* LGECI-297 20201126 add */
					&:focus {
						outline: none;
					}
					/* //LGECI-297 20201126 add */
				}
				color: $color-gray;
				&:not([disabled]):hover, &:not([disabled]):focus {	/* LGECI-297 20201126 edit */
					color: $color-nightrider;
				}
			}
			.slick-dots {
				font-size: 0;
				text-align: center;
				margin: 37px 0 0;
				@include screen(custom, min, $min-md) {
					padding-bottom: 30px;
				}
				@include screen(custom, max, $max-sm) {
					margin: 30px 0 0;
				}
				li {
					display: inline-block;
					width: 12px;
					height: 12px;
					position: relative;
					border-radius: 50%;
					margin-left: 10px;
					line-height:normal;
					&:first-child {
						margin-left: 0;
					}
					@include screen(custom, max, $max-sm) {
						width:10px;
						height:10px;
						margin-left: 8px;
					} 
					a, button {
						background: $color-white;
						display:block;
						width:100%;
						height:100%;
						margin:0;
						padding:0;
						border:$color-dimgray 1px solid;
						border-radius:50%;
						// &:focus {
						// 	outline:1px dotted;
						// 	outline:5px auto -webkit-focus-ring-color;
						// }
						&:focus {		
							outline: 2px solid #000 !important;/* LGECI-297 : 20201214 edit */
						}
					}
					button{
						color:$color-white;
					}
					&.slick-active {
						a, button {
							background: $color-carmine;
						}
					} 
					&:only-child {
						display:none;
					}
				}
			}
		}
	}
}
