.support-title {
	position:relative;
	width:auto;
	height:260px;
	max-width:1600px;
	margin:0 auto ;
	&.bg-white {
		background-color:#000 !important;
	}
	@include screen(custom, max, 1600) {
		margin:0 -15px;
	}
	@include screen(custom, max, $max-sm) {
		height:220px;
	}
	.img-wrap {
		width:100%;height:100%;
		overflow:hidden;
	}
	.img-area {
		position:relative;width:100%;height:100%;
		max-width:(1284px+30px);
		margin:0 auto;
		z-index:0;
		img.pc {
			position:absolute;
			left:50%;top:0;
			margin-left:-800px;
			display:inline;
			@include screen(custom, max, $max-sm) {
				display:none;
			}
		}
		img.mobile {
			position:absolute;
			left:50%;top:0;
			margin-left:(-768px/2);
			display:none;
			@include screen(custom, max, $max-sm) {
				display:inline;
			}
		}
	}
	.txt-area {
		position:absolute;
		left:0;top:0;width:100%;height:100%;
		@include screen(custom, max, $max-sm) {
			padding:0 15px;
			height:auto;
			top:50%;
			transform: translateY(-50%);
		}
		.txt-wrap {
			max-width:(1284px+30px);
			margin:0 auto 50px;
			@include screen(custom, max, $max-sm) {
				margin:0 9px 0;
			}
		}
		.title {
			/* LGEUA-61 20201026 START */
			display: block;
			/* //LGEUA-61 20201026 END */
			text-align:center;
			font-size:48px;line-height:56px;color:$color-white;
			@include font-family($font-semibold);
			padding:78px 0 0 0;
			@include screen(custom, max, $max-sm) {
				//max-width:292px;
				max-width:300px;
				margin:0 auto;
				font-size:32px;line-height:40px;
				padding:0;
			}
			/*LGEJP-199 start*/
			&.email-jp{
				padding:55px 0 0 0;
				@include screen(custom, max, $max-sm) {
					padding:0;
					+.desc{
						max-width: 100%;
						padding: 6px 0 10px 0;
					}
				}
			}
			/*LGEJP-199 end*/
		}
		.desc {
			text-align:center;
			font-size:18px;line-height:24px;color:$color-white;
			@include font-family($font-regular);
			padding:3px 0 11px 0;
			@include screen(custom, max, $max-sm) {
				max-width:270px;
				margin:0 auto;
				padding:6px 0 11px 0;
				font-size:16px;line-height:22px;
			}
			// 20200813 LGECI-134 
			&:empty {
				padding: 0; line-height: 0;
			}
		}
		/*LGEJP-199 start*/
		.sub-desc{
			text-align: center;
			font-size: 16px;
			color: #fff;
			font-weight: bold;
			padding-top: 9px;
			@include screen(custom, max, $max-sm) {
				padding:0;
				font-size:12px;
			}
		}
		/*LGEJP-199 end*/
	}
	
	/* LGEBR-286 START */ 
	&.text-darkgray{
		background-color: $color-white;
		.txt-area{
			.txt-wrap{
				.title, .desc {
					color: #333;
				}
			}
		}
	}
	/* LGEBR-286 END */
}
@include screen(print) {
	.support-title {display:none;}
}