@charset 'UTF-8';

@import './setting/variables';

// container
.support-wrap {
	// @import './support/support-search-area';	//support-solutions.scss 중복
	@import './support/notices-bar';
	@import './support/solutions-contents';
	@import './support/main-thumbnail-link-menu';
	@import './support/promotion-area';
	@import './support/banner-full';
	@import './modal/form-area';
	@import './modal/find-my-model';
	@import './support/no-content';
	@import './support/home-hero';
	@import './support/home-carousel'; //LGEPE-73 20210106 add
	@import './support/promotion-slide'; //LGEBR-572
	
	.accessiblity-help {
		max-width:848px;
		text-align:center;
		font-size: 16px;
		line-height: 26px;
		color: #6b6b6b;
		margin: 0 auto;
		padding:0 0 64px;
		@include screen(custom, max, $max-sm){
			padding:0 0 26px;
		}
	}

		// [Start] LGEMS-175 support home type2
		&.support-home-type2 {
			@import './support/support-title';
		
			//Service Menu
			.service-container {
				$max-width: 1284;
				max-width: $max-width+px;
				margin: 30px auto 0;
				padding: 0 9px;
				color: #333;
				@include screen(custom, min, $min-md) {
					padding: 0 15px;
				}
				@include screen(custom, min, $max-width) {
					padding: 0;
				}
				.info-text {
					margin-bottom: 10px;
					line-height: 24px;
					font-size: 20px;
					font-weight: 600;
					@include screen(custom, min, $min-md) {
						font-size: 24px;
						margin-bottom: 18px;
					}
				}
		
				.service_menus {
					$pd: 12px;
					display: flex;
					flex-flow: row wrap;
					justify-content: flex-start;
					margin-left: -$pd;
					margin-right: -$pd;
					color: red;
		
					.menu-item {
						flex: 0 0 50%;
						max-width: 50%; //ie11 fix
						margin-bottom: 24px;
						padding: 0 $pd;
						@include screen(custom, min, $min-sm) {
							flex: 0 0 33.3%;
							max-width: 33.3%; //ie11 fix
						}
						@include screen(custom, min, $min-lg) {
							flex: 0 0 25%;
							max-width: 25%; //ie11 fix
						}
		
						a {
							display: block;
							padding: 20px 10px 0;
							height: 138px;
							background-color: #f8f8f8;
							border: 1px solid #e4e4e4;
							border-radius: 3px;
							text-align: center;
							@include screen(custom, min, $min-md) {
								height: 150px;
							}
		
							img {
								width: 48px;
								height: 48px;
								margin-bottom: 8px;
								@include screen(custom, min, $min-md) {
									width: 64px;
									height: 64px;
									margin-bottom: 9px;
								}
							}
							span {
								display: block;
								font-size: 14px;
								font-weight: 600;
								@include screen(custom, min, $min-md) {
									font-size: 16px;
								}
							}
						}
					}
				}
		
			}
		}
		// [End] LGEMS-175 support home type2
}

// notice bar RTL
[dir=rtl] {
	.notices-bar-wrap {
		.notices-list-wrap {			
			// notices bar carousel column layout (only for desktop & tablet)
			@include screen(custom, min, $min-md){				
				.notices-list {
					margin-right:0px;
					margin-left:-117px;
				}
				.carousel-btn-wrap {					
					right:auto;
					left: 0;
				}
			}
		}

		.notices-bar {				
			.title {			
				left: auto;
				right:0px;
				line-height: 26px;
				@include screen(custom, max, $max-sm) {
					margin: 13px 0 5px 0;
				}
			}
			// column style layout (only for desktop & tablet)
			@include screen(custom, min, $min-md) { // 768 ~			
				.title {				
					margin-right:0px;
					margin-left:30px;				
				}
			}
		}
	}

	.notices-bar-wrap .notices-list .list-item .date {
		right:auto;
		left: 0;
	}

	.notices-bar-wrap .notices-list .list-item .subject {
		padding-right:0px;
		padding-left:83px;
	}

	.carousel-btn-wrap .type-square.slick-next {
		background-image: url(/lg5-common-gp/images/common/icons/arrow-left-dimgray.svg);
		background-image: url(/lg5-common-gp/images/common/icons/arrow-left.svg);		
	}

	.carousel-btn-wrap .type-square.slick-prev {
		background-image: url(/lg5-common-gp/images/common/icons/arrow-right-dimgray.svg);
		background-image: url(/lg5-common-gp/images/common/icons/arrow-right.svg);
	}
}