.modal {
	.modal-form-area {
		padding: 20px;
		@include screen(custom, max, $max-sm) {
			padding: 12px;
		}
		background-color: $bg-whitesmoke;
		.field-block {
			margin-top: 12px;

			&:first-child {
				margin-top: 0;
			}
			/* LGEAU-250 20210114 add */
			&.check-block {
				border-top: 1px solid $line-gray;
				margin-top: 15px;
				padding: 20px 0 5px;
				[dir="rtl"] & .checkbox-box {
					display: block;
				}
				.text{
					vertical-align: top;
				}
				@include screen(custom, max, $max-sm) {
					padding-left: 34px;
					.checkbox-box{
						margin-left: -34px;
					}
				}
			}
			/* //LGEAU-250 20210114 add */
			.required-text {
				display: none;
				color: $color-carmine;
				font-size: 14px;
				line-height: 24px;
				margin:2px 0 0 10px;
			}

			&.required {
				.required-text {
					display: block;
				}
			}
		}
		// description text
		.desc {
			@include font-family($font-semibold);
			font-size: 16px;
			line-height: 24px;
			color: $color-nightrider;
		}
	}
	.migration-contents {
		box-sizing: border-box;
		border: 1px solid $line-gray;
		color: $color-dimgray;
		font-size: 16px;
		line-height: 24px;
		padding: 19px;
		margin-top: 10px;

		.find-img {
			img {
				max-width: 100%;
				display: block;
				margin: 0 auto;
			}
			& + * {margin-top: 25px;}
		}
		
		display: none;
		&.active {
			display: block;
		}
	}
	// 20200429 START 구유정 || rtl 추가
	[dir=rtl] & { // .modal
		.modal-form-area {
			.field-block {
				.required-text {
					margin-left:0;
					margin-right:10px;
				}
			}
		}
	}
	// 20200429 END 구유정 || rtl 추가
}


//LGEPA-317
#modal_find_my_model{
	a{
		&.find-img{
		cursor: url(/lg5-common-gp/images/components/zoomin_cursor.png),
				url(/lg5-common-gp/images/components/zoomin_cursor.cur),pointer;
		}
	}
	.zoom-active{
		display:none;
	}
	.modal-zoom{
		position: absolute;
		left: 0;
		right: 0;
		top: 50%;
		width:710px;
		height:590px;
		border:1px solid #797979;
		background:#fff;
		margin: -295px auto 0;
		padding: 55px 50px 0 0;
		display:none;
		@include screen(custom, max, $max-sm) {	
			width: 100%;
			height: 617px;
			padding: 90px 0 0 0 !important;
			margin: 0 auto;
			top: 42px;
		}
		[dir=rtl] & {
			padding: 55px 0 0 50px;
		}
		&.active {
			display: block;
		}
		.zoom-area{
			max-width: 590px;
			width: 100%;
			height: 475px;
			margin: 0 auto;
			position: relative;
			overflow: hidden;
			cursor: url(/lg5-common-gp/images/components/zoomin_cursor.png),
					url(/lg5-common-gp/images/components/zoomin_cursor.cur),pointer;
			img{
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				margin: auto;
				max-width: 100%;
				max-height: 100%;
				transition: transform 200ms ease-in-out 0s;
				&.pc{
					display:block;
				}
				&.mobile{
					display:none;
				}
				@include screen(custom, max, $max-sm) {	
					transition: none;
					&.pc{
						display:none;	
					}
					&.mobile{
						display:block;
						margin: 0 auto;
					}
				}
			}
		}
		.zoom-close{
			cursor: pointer;
			width: 20px;
			height: 20px;
			padding: 0;
			border: 0;
			overflow: hidden;
			white-space: nowrap;
			background: url(/lg5-common-gp/images/common/icons/modal-close.svg) no-repeat 0 0;
			position: absolute;
			z-index: 1;
			top: 22px;
			right: 30px;
			text-indent: -9999px;
			@include screen(custom, max, $max-sm) {	
				right: 20px;
				top: 17px;
				width: 15px;
				height: 15px;
				background-size: 100% 100%;
			}
			[dir=rtl] & {
				right: auto;
				left: 30px;
				@include screen(custom, max, $max-sm) {	
					left: 20px;
				}
			}
		}
		.control {
			position:absolute;
			top:50%;
			width:30px;
			margin-top:-45px;
			z-index:5;
			@include screen(custom, min, $min-md) {
				right:30px;
				padding-left:1px;
			}
			@include screen(custom, max, $max-sm) {
				right:auto;
				left:50%;
				top:30px;
				width:97px;
				height:40px;
				margin:0 0 0 -52px;
			}
			[dir="rtl"] & {
				@include screen(custom, min, $min-md) {
					right:auto;
					left:30px;
					padding-left:0;
					padding-right:-1px;
				}
				@include screen(custom, max, $max-sm) {
					left:auto;
					right:50%;
					margin:0 -52px 0 0;
				}
			}
			&:before{
				content:"";
				position:absolute; 
				top:50%;
				left:0;
				width:30px;
				margin-top:-9px;
				border-bottom:1px solid $line-gray;
				@include screen(custom, max, $max-sm) {
					top:0;
					left:50%;
					width:auto;
					height:40px;
					border-right:1px solid $line-gray;
					border-bottom:none;
					margin:0 0 0 2px;
					[dir="rtl"] & {margin:0 0 0 -2px;}
				}
			}
			button {
				touch-action: manipulation;
				cursor:pointer;
			}
			.plus{
				overflow:hidden;
				color:transparent;
				display:block;
				width:28px; 
				height:28px;
				margin-bottom:35px;
				border:none;
				background:url(/lg5-common-gp/images/common/icons/zoomin.svg) center no-repeat;
				text-indent:-9999em;
				[dir="rtl"] & {text-indent:9999em;}
				@include screen(custom, max, $max-sm) {
					margin-top:6px;
					margin-bottom:0;
					float:right;
					[dir="rtl"] & {float:left;}
				}
			}
			.minus{
				overflow:hidden;
				color:transparent;
				display:block;
				width:28px; 
				height:28px;
				margin-bottom:17px;
				border:none;
				background:url(/lg5-common-gp/images/common/icons/zoomout.svg) center no-repeat;
				text-indent:-9999em;
				[dir="rtl"] & {text-indent:9999em;}
				@include screen(custom, max, $max-sm) {
					margin-top:6px;
					margin-bottom:0;
					margin-left:6px;
					float:left;
					[dir="rtl"] & {
						float:right;
						margin-left:0;
						margin-right:6px;
					}
				}
			}
			
		}
		img.lazyload {opacity:1;}
	}
}