.support-banner-full {
	position:relative;
	margin:0 -15px;
	.visual {
		position: relative;
		width:100%;
		height:400px;
		overflow: hidden;
		img {
			position: absolute;
			top:0;
			left:50%;
			transform:translateX(-50%);
		}
		@include screen(custom, max, $max-sm) {
			height:260px;
		}
	}
	.text-block {
		position:absolute;
		top:50%; left:50%;
		transform: translate(-50%,-50%);
		max-width:1284px;
		margin:auto;
		width:100%;
		@include screen(custom, max, $max-sm) {
			position:static;
			height:auto;
			width:100%;
			transform: none;
			padding:0 24px;
		}
		.vertical-wrap {
			padding:0 109px*100%/1284px;
			@include screen(custom, max, $max-sm) {
				padding:20px 0;
			}
		}
		.name {
			margin:0;
			color:$color-black;
			@include font-family($font-semibold);
			font-size:40px;
			line-height:48px;
			@include screen(custom, max, $max-lg) {
				font-size:34px;
				line-height:40px;
			}
			@include screen(custom, max, $max-md) {
				font-size:28px;
				line-height:36px;
			}
			@include screen(custom, max, $max-sm) {
				font-size:24px;
				line-height:36px;
			}
		}
		.info {
			// flexible declaration
			// max-width:50%;

			// fixed declaration
			margin-top: 6px;
			@include font-family($font-regular);
			font-size:16px;
			line-height:24px;
			color:$color-nightrider;
			@include screen(custom, min, $min-md) {
				width:290px;
			}
			@include screen(custom, max, $max-sm) {
				// max-width:none;
				font-size:14px;
				line-height:20px;
				margin-top: 4px;
			}
		}
		.btn-area {
			margin-top:24px;
			@include screen(custom, max, $max-sm) {
				margin-top:19px;
				.btn {width:100%;}
			}
		}
		&.white {
			.name {color:$color-white;}
			.info {color:$color-white;}
		}
	}
}