@charset 'UTF-8';
@import './setting/variables';

// LGEBR-572
.promotion-slide-wrap {
	margin: 26px 0 50px;
	padding: 0 80px;
	@include screen(custom, max, $max-sm) {
		margin: 20px -15px 0;
		padding: 5px 0 15px;
	}
	.promotion-inner-box {
		position: relative;
		margin: 0 auto;
		max-width: 1284px;
	}
	.promotion-items {
		text-align: center;
		@include screen(custom, max, $max-sm) {
			margin: 0;
		}
		.items {
			@extend %clearfix;
			width: 100%;
			opacity: 0;
			&.slick-initialized {
				opacity: 1;
			}
		}
		.item {
			float: left;
			margin: 0 15px;
			outline: none;
			[dir="rtl"] & {
				float: right;
			}
			@include screen(custom, max, $max-sm) {
				margin: 0 15px;
			}
			@include screen(custom, max, 320) {
				margin: 0 8px;
			}
			@include screen(custom, min, $min-lg) {
				&:nth-child(4n-3) {&:before {display: none;}}
			}
			@include screen(md) {
				&:nth-child(3n-2) {&:before {display: none;}}
			}
			@include screen(custom, max, $max-sm) {
				&:nth-child(2n-1) {&:before {display: none;}}
			}
			a {
				display: block;
				max-width: 100%;
				margin: 1px auto;
				border: 1px solid $line-gray;
				padding: 35px 20px 30px;
				border-radius: 10px;
				background-color: $bg-white;
				@include screen(custom, max, $max-sm) {
					max-width: 100%;
					padding: 35px 10px 30px;
				}
			}
			.name {
				margin-bottom: 19px;
				height: (26px*2);
				font-size: 20px;
				line-height: 26px;
				color: $color-nightrider;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				word-wrap: break-word;
				@include font-family($font-semibold);
				[dir="rtl"] & {
					display: block;
				}
				@include screen(custom, max, $max-sm) {
					margin-bottom: 14px;
				}
			}
			.visual {
				img {
					max-width: 180px;
				}
				margin-bottom: 35px;
				@include screen(custom, max, $max-sm) {
					padding: 0 15px;
					margin-bottom: 30px;
					box-sizing: border-box;
					img {
						width: auto !important;
					}
				}
			}
			.info {
				margin: 0;
				height: (24px*3);
				font-size: 16px;
				line-height: 24px;
				color: $color-dimgray;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 3;
				-webkit-box-orient: vertical;
			}
		}
	}

	// slick carousel stylesheet
	.slick-track {
		margin: 0 auto;
	}
	.slick-slider {
		button {
			appearance: none;
			border: none;
			background-color: transparent;
		}
		.slick-list {
			overflow: hidden;
			width: auto;
			@include screen(custom, max, $max-sm) {
				padding: 0 45px;
			}
		}
		.slick-arrow {
			overflow: hidden;
			position: absolute;
			top: 50%;
			padding: 0;
			width: 22px;
			height: 40px;
			transform: translate(0,-50%);
			text-indent: 10em;
			z-index: 99;
			@include screen(custom, max, $max-sm) {
				width: 12px;
				height: 22px;
			}
			[dir="rtl"] & {
				transform: translate(0,-50%) rotate(180deg);
			}
			&:not([disabled]):hover,
			&:not([disabled]):focus {
				color: $color-black;
				cursor: pointer;
			}
			&.slick-prev{
				left: -50px;
				background: url('/lg5-common-gp/images/common/icons/carousel-left-over.svg') 0 0;
				background-size: 22px 40px;
				[dir="rtl"] & {
					left: auto;
					right: -50px;
				}
				@include screen(custom, max, $max-sm) {
					left: 38px;
					background-size: 12px 22px;
					[dir="rtl"] & {
						left: auto;
						right: 38px;
					}
				}
			}
			&.slick-next {
				right: -50px;
				background: url('/lg5-common-gp/images/common/icons/carousel-right-over.svg') 0 0;
				background-size: 22px 40px;
				[dir="rtl"] & {
					right: auto;
					left: -50px;
				}
				@include screen(custom, max, $max-sm) {
					right: 38px;
					background-size: 12px 22px;
					[dir="rtl"] & {
						left: 38px;
						right: auto;
					}
				}
			}
			&.slick-disabled {
				opacity: 0.3;
				cursor: default;
			}
			svg {
				display: block;
			}
		}
		.slick-dots {
			text-align: center;
			margin-top: 18px;
			height: 10px;
			li {
				position: relative;
				display: inline-block;
				vertical-align: top;
				width: 10px;
				height: 10px;
				margin: 0 4px;
				line-height: normal;
				border-radius: 50%;
				border: 1px solid $color-gray;
				a, button {
					background: $color-white;
					display: block;
					width: 100%;
					height: 100%;
					margin: 0;
					padding: 0;
					border: 0;
					border-radius: 50%;
					overflow: hidden;
					color: transparent;
					cursor: pointer;
					&:focus {
						outline: 1px dotted;
						outline: 5px auto -webkit-focus-ring-color;
					}
				}
				&.slick-active {
					border: 1px solid $color-carmine;
					a, button {
						background: $color-carmine;
					}
				}
				&:only-child {
					display: none;
				}
			}
		}
	}
}